import Vue from '@/components/base'
import { Component } from 'vue-property-decorator'
import template from './BasicFont.vue'

@Component({
    name: 'BasicFont',
    mixins: [template]
})
export default class BasicFont extends Vue {
    //#region datas ******************************/
    protected fontWeightList = ['regular', 'medium', 'bold']
    //#endregion datas ***************************/

    //#region props ******************************/
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    //#endregion event handler *******************/

    //#region methods ****************************/
    // 根据 css 变量名称获取变量值
    protected getVarValue(varName: string) {
        let body = document.body

        if (body) {
            return getComputedStyle(body).getPropertyValue(varName)
        }
    }
    //#endregion methods *************************/
}
